<template>
  <a class="stock-item" :href="stock.url" @click="onClick" target="_blank">
    <div class="user">
      <span class="ph"
            :style="{ 'background-image': `url(${stock.user.profile_image_url})` }"></span>
      <span class="text">
        <span class="name">{{ stock.user.name ? stock.user.name : stock.user.id }}</span>
        {{ createAt }}
      </span>
    </div>
    <p class="title">{{ stock.title }}</p>
    <div class="counts">
      <span class="icon-like">
        <img src="../assets/icon_like.svg" width="14" height="14" alt="">
      </span>
      <span> {{ stock.likes_count }}</span>
      <span class="icon-comment">
        <img src="../assets/icon_comment_alt.svg" width="14" height="14" alt="">
      </span>
      <span> {{ stock.comments_count }}</span>
    </div>
  </a>
</template>

<script>
import moment from 'moment';

export default {
  props: ['stock'],
  computed: {
    createAt() {
      return moment(this.stock.created_at).format('YYYY/MM/DD HH:mm');
    },
  },
  methods: {
    onClick() {
      this.$ga.event({
        eventCategory: 'Stocks',
        eventAction: 'Link Click',
        eventLabel: this.stock.url,
        eventValue: 1,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../styles/theme.scss';

a.stock-item {
  border-radius: 3px;
  text-decoration: none;
  color: #333;
  box-shadow: 0 2px 3px 2px rgba(0, 0, 0, .1);
  padding: 10px;
  flex: 1;
  transition: all .3s;
  display: flex;
  flex-direction: column;
  &:hover {
    box-shadow: 0 2px 8px 2px rgba(0, 0, 0, .3);
  }
}

.user {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  .ph {
    display: inline-block;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    overflow: hidden;
    background-size: cover;
    border: 2px solid $primary;
  }
  .text {
    font-size: 12px;
    margin-left: 10px;
    line-height: 1.2;
  }
  .name {
    display: block;
    font-weight: bold;
    margin-bottom: 2px;
  }
}

.title {
  flex: 1;
  padding: 5px 0;
  font-size: 13px;
  line-height: 1.3;
}

.counts {
  .icon-comment {
    margin-left: 8px;
  }
  span {
    margin-right: 5px;
    font-size: 11px;
    display: inline-block;
  }
}
</style>



